body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background:linear-gradient(to right, #0f1413 0%, #172825 50%, #0f1413 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );height:auto}
.home-box{background:#192325;background:linear-gradient(to right, #0f1413 0%, #172825 50%, #0f1413 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#01d3130", endColorstr="#019664",GradientType=1 );height:100vh;display:flex;flex-direction:column;justify-content:space-between}.home-links-box{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;z-index:2;padding-top:50px}.particles{position:absolute;width:100%;height:100%}@media(min-width: 812px){.home-links-box{flex-direction:row}}
header{margin:0 auto;padding:10px 1vw;display:inline-flex;justify-content:space-between}header .logo-svg{width:40px;height:40px;opacity:.8}header .logo-svg .cls-1{fill:#10c286}header .logo-svg:hover{opacity:1}
.nav-box{width:auto;height:auto;margin-right:.5vw}.nav-svg{width:30px;height:30px;fill-opacity:.8;float:right;margin-right:0}.nav-svg .cls-1{fill:#10c286}.nav-svg:hover{fill-opacity:1;cursor:pointer}.nav-list{display:flex;flex-direction:column;align-items:flex-end;width:100%}.nav-link{margin:2px;padding:5px;background-color:rgba(0,0,0,.432)}.nav-link a{font-family:"Rubik";font-weight:bold;text-transform:uppercase;-webkit-text-emphasis:none;text-emphasis:none;text-decoration:none;color:#17473d;padding:2px;border:none 2px #10c286}.nav-link a:hover{color:#10c286;border:solid;transition-duration:.2s;transition-timing-function:ease-out;margin-right:5px}
.home-link{display:flex;justify-content:center;align-items:center;width:110px;height:60px;font-size:150%;margin:0 30px}.home-link span{color:#10c286;height:100%}.home-link span:hover{color:#136952;transition-duration:.3s;transition-timing-function:ease-in-out}.home-link a{color:#18584a;text-decoration:none;font-family:"Rubik";font-weight:bold;height:100%;padding:1px 1px 0 1px;text-transform:uppercase}.home-link a:hover{color:#10c286;transition-duration:.3s;transition-timing-function:ease-in-out}@media(min-width: 812px){.home-link:hover a{border-color:#10c286;border-bottom:solid 3px;transition-duration:.2s;transition-timing-function:ease-in-out}}

.content-type-box{height:600px}
.about-box{height:100vmin;width:80vw;margin:20px auto;background-color:rgba(23,32,32,.404);color:#019664;border:solid 2px #1c2e2a}
